import React from 'react'
import Link from 'gatsby-link'

import Chilly from '../components/Chilly'

const Links = [
  {
    to: "http://google.com",
    text: "Google"
  },
  {
    to: "http://google.com",
    text: "Google"
  },
  {
    to: "http://google.com",
    text: "Google"
  },
  {
    to: "http://google.com",
    text: "Google"
  },
]

class Template extends React.Component {

  constructor() {
    super()
    this.state = {
      chillyActive: false
    }
    this.closeChilly = this.closeChilly.bind(this)
    this.openChilly = this.openChilly.bind(this)
  }

  closeChilly() {
    console.log("Closing Chilly")
    this.setState({
      chillyActive: false
    })
  }

  openChilly() {
    console.log("Opening Chilly")
    this.setState({
      chillyActive: true
    })
  }

  render() {
    const { location, children } = this.props
    return (
      <div>
        <div onClick={this.closeChilly}>
          {children()}
        </div>
        <Chilly onClick={this.openChilly} open={this.state.chillyActive} links={Links}/>
      </div>
    )
  }
}

Template.propTypes = {
  children: React.PropTypes.func,
  location: React.PropTypes.object,
  route: React.PropTypes.object,
}

export default Template



